import React, { Component } from 'react'

import './PersonChat.scss'

class PersonChat extends Component {
  state = {
    images: false,
  }

  render() {
    const {
      name,
      message,
      image,
      changeCurrentUser,
      online,
      muted,
      read,
      time,
    } = this.props
    return (
      <div
        className={`person-chat ${read && 'read'}`}
        onClick={changeCurrentUser}
      >
        <img src={image} alt="" />
        <div className="name-and-text">
          <div className="name">
            {name}
            {muted && <i className="fas fa-volume-mute" />}
            {online && <div className="online-dot" />}
          </div>
          <p className="text">
            {message && message.length > 50
              ? `${message.substr(0, 50)}...`
              : message}
          </p>
        </div>
        <div className="time">{time}</div>
      </div>
    )
  }
}

export default PersonChat
