import React, { Component } from 'react'
import LeftSide from '../LeftSide/LeftSide'
import RightSide from '../RightSide/RightSide'
import './MessageBlockMain.scss'

class MessageBlockMain extends Component {
  state = {
    users: [],
    currentUser: '',
  }
  importAll = r => {
    let images = {}
    r.keys().map(item => (images[item.replace('./', '')] = r(item)))
    return images
  }
  componentWillMount() {
    const images = this.importAll(
      require.context('../../img', false, /\.(png|jpe?g|svg)$/)
    )
    console.log(images)
    this.setState({ images })
  }
  componentDidMount() {
    localStorage.getItem('users')
      ? this.setState({ users: JSON.parse(localStorage.getItem('users')) })
      : fetch('./chat-info.json')
          .then(res => res.json())
          .then(res => this.setState(res))
  }
  componentDidUpdate() {
    localStorage.setItem('users', JSON.stringify(this.state.users))
  }

  sendMessage = (e, text) => {
    e.preventDefault()
    let time = new Date()
      .toTimeString()
      .split(' ')[0]
      .split(':')
    time = `${time[0]}:${time[1]}`
    const { currentUser } = this.state
    const newMess = {
      id: this.state.users[currentUser].messages.length - 1,
      text,
      time,
      whose: 'my',
    }
    const update = [...this.state.users]
    update[currentUser].messages.push(newMess)
    this.setState({
      users: [...update],
    })
  }

  changeCurrentUser = id => {
    const update = [...this.state.users]
    update[id].read = true
    this.setState({
      currentUser: id,
      users: update,
    })
  }

  render() {
    const { users, images, currentUser } = this.state
    return (
      <div className="message-block__container">
        <div className="message-block__main">
          <LeftSide
            users={users}
            images={images}
            changeCurrentUser={this.changeCurrentUser}
          />
          <RightSide
            currentUser={users[currentUser]}
            images={images}
            sendMessage={this.sendMessage}
          />
        </div>
      </div>
    )
  }
}

export default MessageBlockMain
