import React, { Component } from 'react'
import PersonChat from '../../Components/PersonChat/PersonChat'

import './LeftSide.scss'

class LeftSide extends Component {
  render() {
    const { users, images, changeCurrentUser } = this.props
    return (
      <div className="left-side">
        {users.map((user, i) => (
          <PersonChat
            key={`${user.name}_${i}`}
            name={user.name}
            message={user.messages[user.messages.length - 1].text}
            time={user.messages[user.messages.length - 1].time}
            image={images[user.image]}
            online={user.online}
            muted={user.muted}
            read={user.read}
            changeCurrentUser={() => changeCurrentUser(user.id)}
          />
        ))}
      </div>
    )
  }
}

export default LeftSide
