import React from 'react'

import './Message.scss'

const Message = ({ whose, text, time, images }) => {
  return (
    <div className={`${whose}-message`}>
      {whose === 'user' && <img src={images} alt="img" />}
      <div className="message-text">
        <p>{text}</p>
        <p className="message-time">{time}</p>
      </div>
    </div>
  )
}

export default Message
